import { RESTART_TRANSACTION_RESPONSE, TRANSACTION_LOADING, 
    TRANSACTION_SUCCESS,
    TRANSACTION_ERROR, TRANSACTION_CODE_ERROR, RESTART_TRANSACTION_RETRY_RESPONSE, TRANSACTION_RETRY_LOADING, TRANSACTION_RETRY_SUCCESS, TRANSACTION_RETRY_ERROR, TRANSACTION_RETRY_CODE_ERROR } from "../ActionTypes/UserTransactionActionTypes";
import { TransactionListService, retryTransactionService } from "../../services/UsersTransactionDetailService";

//perPage,pageIndex,input,startDate,endDate
export const viewTransactionDetailsAction = (perPage,pageIndex,input,startDate,endDate) =>
{
return (dispatch)=>{

 dispatch({type:RESTART_TRANSACTION_RESPONSE});
 dispatch({type:TRANSACTION_LOADING});

  TransactionListService(perPage,pageIndex,input,startDate,endDate).then((res)=>{

         console.log(res);

         if(res.hasOwnProperty('success') && res.success==true){
          dispatch({type:TRANSACTION_SUCCESS,res});
        

         }else if(res.hasOwnProperty('success') && res.success==false) {
              dispatch({type:TRANSACTION_ERROR,res});  
         }
     },
     error=>{
      dispatch({type:TRANSACTION_CODE_ERROR,error});
         console.log(error);
     }
 )
} 

}

export const clearEventListState= ()=>
{
return (dispatch)=>
{
 dispatch({type:RESTART_TRANSACTION_RESPONSE});
}
}


export const retryTransactionAction = (credentials) =>
{
return (dispatch)=>{

 dispatch({type:RESTART_TRANSACTION_RETRY_RESPONSE});
 dispatch({type:TRANSACTION_RETRY_LOADING});

  retryTransactionService(credentials).then((res)=>{

         console.log(res);

         if(res.hasOwnProperty('success') && res.success==true){
          dispatch({type:TRANSACTION_RETRY_SUCCESS,res});
        

         }else if(res.hasOwnProperty('success') && res.success==false) {
              dispatch({type:TRANSACTION_RETRY_ERROR,res});  
         }
     },
     error=>{
      dispatch({type:TRANSACTION_RETRY_CODE_ERROR,error});
         console.log(error);
     }
 )
} 

}

export const clearRetryTransactionState= ()=>
{
return (dispatch)=>
{
 dispatch({type:RESTART_TRANSACTION_RETRY_RESPONSE});
}
}