import HttpService from './HttpService';
import Cookies from 'js-cookie'




export const TransactionListService = (perPage,pageIndex,input,startDate,endDate) =>
{
    const http = new HttpService();
    
    let addedUrl = "";
    /**
     * if input and options select is empty
     */
    if((input==null || input=="") && (endDate==null || endDate=="") )
    {
        addedUrl = "admin/get-transaction-details/"+perPage+"?page="+pageIndex;
    }
    else if((input!=null || input !="") && (endDate==null || endDate=="") )
    {
        addedUrl = "admin/filter-transaction-details-by-input/"+input+"/"+perPage+"?page="+pageIndex;
    }
    else if((input==null || input =="") && (endDate!=null || endDate!=""))
    {
        addedUrl = "admin/filter-transaction-details-by-date/"+startDate+"/"+endDate+"/"+perPage+"?page="+pageIndex;
    }
    else if((input!=null || input !="") && (endDate!=null || endDate!=""))
    {
        //filter-transaction-details-by-date-and-input/{input}/{startDate}/{endDate}/{pager}
        addedUrl = "admin/filter-transaction-details-by-date-and-input/"+input+"/"+startDate+"/"+endDate+"/"+perPage+"?page="+pageIndex;
    }
    
    let tokenId = 'vt-music';
    return http.getData(addedUrl,tokenId).then((data)=>
    {
        console.log(data)
        return data;

    }).catch((error)=>
    {
        console.log(error)
        return error;
    });
}



export const retryTransactionService = (credentials) =>
{
    const http = new HttpService();
    let addedUrl = "admin/retry-transactions";
    let tokenId = 'vt-music';
    return http.postData(credentials,addedUrl,"POST",tokenId).then(data=>{
        console.log(data);
        console.log(JSON.stringify(data));
        return data;
    }).catch((error)=> {console.log(error)
        return error; 
         });
}