import { 
    RESTART_BANK_NAME_RESPONSE,
    BANK_NAME_LOADING, 
    BANKLIST_SUCCESS, 
    BANKLIST_ERROR, 
    BANK_NAME_CODE_ERROR, 
    BANK_NAME_ERROR,  
    BANK_NAME_SUCCESS, 
    RESTART_BANKLIST_RESPONSE,
    BANKLIST_LOADING,
    BANKLIST_CODE_ERROR,
    
  } from "../ActionTypes/UserBankDetailTypes";

import { getBankDetailsListService,
        BankNamesOptionService } from "../../services/UserBankDetailsService";


export const loadBankNameOptions = () =>
{
return (dispatch)=>{

 dispatch({type:RESTART_BANK_NAME_RESPONSE});
 dispatch({type:BANK_NAME_LOADING});

BankNamesOptionService().then((res)=>{

         console.log(res);

         if(res.hasOwnProperty('success') && res.success==true){
          dispatch({type:BANK_NAME_SUCCESS ,res});
        

         }else if(res.hasOwnProperty('success') && res.success==false) {
              dispatch({type:BANK_NAME_ERROR,res});  
         }
     },
     error=>{
      dispatch({type:BANK_NAME_CODE_ERROR,error});
         console.log(error);
     }
 )
} 

}

export const clearBankNamesOptionService= ()=>
{
return (dispatch)=>
{
 dispatch({type:RESTART_BANK_NAME_RESPONSE});
}
}



export const viewBankListAction = (perPage,pageIndex,input,options) =>
{
return (dispatch)=>{

 dispatch({type:RESTART_BANKLIST_RESPONSE});
 dispatch({type:BANKLIST_LOADING});

getBankDetailsListService(perPage,pageIndex,input,options).then((res)=>{

         console.log(res);

         if(res.hasOwnProperty('success') && res.success==true){
          dispatch({type:BANKLIST_SUCCESS ,res});
        

         }else if(res.hasOwnProperty('success') && res.success==false) {
              dispatch({type:BANKLIST_ERROR,res});  
         }
     },
     error=>{
      dispatch({type:BANKLIST_CODE_ERROR,error});
         console.log(error);
     }
 )
} 

}

export const clearBankListState= ()=>
{
return (dispatch)=>
{
 dispatch({type:RESTART_BANKLIST_RESPONSE});
}
}

