import React,{useState,useEffect} from 'react';
import {
  CBadge,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CTooltip,
  CRow,
  CButton,
  CPagination
} from '@coreui/react';
import {
  useDispatch,
   useSelector
  } from 'react-redux'
    
  import {
    useFormFields,
    disableSubmitButton
 }  from '../../../helpers/hooksFormInput'
 import {
     cleanUpData,
     instantiateAbort,
     SwalAlert
    } from '../../../helpers/componentHelperFunc'


    import Loader from 'react-loader-spinner'

import { NavLink } from 'react-router-dom';
import { viewEventDetailsAction } from '../../../store/Actions/UserEventAction';
import { viewTransactionDetailsAction, retryTransactionAction } 
from '../../../store/Actions/UserTransactionAction';






export default function TransactionDetailsLists(props)  
{
   
 

  const dispatch =  useDispatch();
     
  const TransactionList = useSelector(state => state.transactionReducer.transactionResponse);
  const retryTransactionResponse = useSelector(state=>state.transactionReducer.transactionRetryResponse);

   
  const abortEffect = instantiateAbort();

  const perPage = 5;
  
  const [currentPage, setCurrentPage] = useState(1);


  useEffect(() => {
    
    if(retryTransactionResponse!="" && retryTransactionResponse !="loading" 
    && retryTransactionResponse.hasOwnProperty("success"))
    {
      if(retryTransactionResponse.success==false)
      {
       SwalAlert("error","transaction retry failed","error");

       dispatch( viewTransactionDetailsAction(perPage,1,props.filterData.input,
        props.filterData.startDate,props.filterData.endDate) );

      }else if(retryTransactionResponse.success==true)
      {
        SwalAlert("success","transaction retry was successful","success");

        dispatch( viewTransactionDetailsAction(perPage,1,props.filterData.input,
          props.filterData.startDate,props.filterData.endDate)  );
      }
    }

    return () => { 
    }
  }, [retryTransactionResponse])

 //perPage,pageIndex,input,startDate,endDate
   useEffect(() => {
      
        dispatch( viewTransactionDetailsAction(perPage,1,props.filterData.input,
          props.filterData.startDate,props.filterData.endDate)  );
    
     return () => {
       cleanUpData(abortEffect)
     };
   }, [props.filterData])

    useEffect(() => {
      
     dispatch(viewTransactionDetailsAction(perPage,1,null,null,null));
      return () => {
        cleanUpData(abortEffect);
      }
    }, []);


  

  const loadPageItem = (nextOrPrevPageClicked) =>
  {
    if(props.hasOwnProperty("filterData") && props.filterData!=null
     && props.filterData!="")
    {
      dispatch(viewTransactionDetailsAction(perPage,nextOrPrevPageClicked,
        props.filterData.input,props.filterData.startDate,props.filterData.endDate));
  
    setCurrentPage(nextOrPrevPageClicked);
    }else
    {
      dispatch(viewTransactionDetailsAction(perPage,nextOrPrevPageClicked,
        props.filterData.input,props.filterData.startDate,props.filterData.endDate));
  
    setCurrentPage(nextOrPrevPageClicked);
    }
    
  } 

 const retryTransaction = (e,id,userId,amount,transactionId,email) =>
 {
   let credentials = {
     id:id,
     user_id:userId,
     amount:amount,
     transaction_id:transactionId,
     email:email,
     transaction_date_time:(new Date()).getTime()
   }

   dispatch(retryTransactionAction(credentials));
 }


  return (
    <>
    <CRow>
        <CCol>
          <CCard>
            <CCardHeader>
            Transaction Lists
            </CCardHeader>
            <CCardBody>
               
               {
                  TransactionList == 'loading' || TransactionList == '' ?

                  <Loader type="ThreeDots" color="#321fdb" height={100} width={100} />
                  :
                  TransactionList.hasOwnProperty('data') && 
                  TransactionList.success == true  && TransactionList.data!=null?
                    <>
<table className="table table-bordered table-striped table-responsive">
    <thead>
      <tr>
        <th style={{minWidth:200}}>Transaction Id</th>
        <th style={{minWidth:200}}>Email</th>
        <th style={{minWidth:200}}>Amount</th>
        <th style={{minWidth:200}}>Status</th>
        <th style={{minWidth:200}}>Transaction Purpose</th>
        <th style={{minWidth:200}}>Created</th>
  
        
      </tr>
    </thead>
    <tbody>
      {  
        TransactionList.data.data.map((data)=>(
  
          <tr key={data.id}>
   
          <td>{data.transaction_id}</td>
          <td>{data.email}</td>
      <td>{""+parseInt(data.amount).toLocaleString()}</td>
      <td>{data.status=="success" || data.status=="successful"?
      <span>{data.status}</span>
      :
        <span>{data.status}&nbsp;&nbsp;&nbsp;<button 
        className="btn btn-danger"
        onClick={(e)=>retryTransaction(e,data.id,data.user_id,data.amount,data.transaction_id,
          data.email)}
        >retry</button></span>
      }</td>
      <td>
        {data.transaction_purpose}
      </td>
       <td>
         {
         data.created_at
         }
       </td>
         
          
    </tr>

        ))
      }
     
      
    </tbody>
  </table>

<div style={{marginLeft:10}}>
            <CPagination
            size="sm"
            activePage={currentPage}
            pages={TransactionList.data.total}
            onActivePageChange={loadPageItem}
            pages={TransactionList.data.last_page}
          />
                 
            
            </div>
            </>
            :

           <h3><b>Oops! No Item Found</b></h3>
            
           }


            </CCardBody>
            
           

          </CCard>
        </CCol>
      </CRow>
    </>
  );
}
  